@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,200&family=Roboto:wght@100;300;400;500;700;900&display=swap');

* {
    margin: 0;
    padding: 0;
    background-color: #151720;
    box-sizing: border-box;
}

body {
    min-height: 100vh;
    overflow-y: auto;
}

.container {
    margin-top: 15px;
    height: 100vh;
}

h1 {
    color: rgb(24, 68, 130);
    font-size: 40px;
    font-weight: 900;
    text-shadow: 2.5px 2.5px rgb(240, 240, 253);
    text-transform: uppercase;
    text-align: center;
    margin: 15px 5px;
    padding: 15px;
}

h3 {
    color: rgb(255, 255, 255);
    font-weight: 800;
}

.form-content {
    height: 70vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px 2px;
}

.form-content-sm {
    height: 65vh;
    width: 50%;
    border: 0.3px solid rgb(169, 169, 169);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 40px;
    box-shadow: 15px 10px 20px rgb(79, 80, 80);
}

.file-container {
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.input-file {
    display: none;
}

.upload-button {
    display: inline-block;
    width: 200px;
    padding: 12px 20px;
    background-color: #03071e;
    color: #fff;
    border: none;
    cursor: pointer;
    border-radius: 20px;
    margin: 5px 10px;
    font-size: 18px;
    font-weight: 700;
    text-align: center;
}

.upload-icon {
    margin: 0 5px;
}

.file-label {
    text-align: center;
    font-size: 25px;
    margin: 25px 10px 10px 10px;
}

.file-name {
    font-size: 14px;
    color: #b7b7b7;
    text-align: center;
    text-transform: uppercase;
}

.upload-container {
    width: 100%;
    max-width: 100%;
    margin: 20px 0 20px 0;
    padding: 20px 0 40px 0;
    position: relative;
}

.close-button {
    font-size: 20px;
    color: white;
    border: none;
    position: absolute;
    top: 10%;
    right: 20%;

}

h4 {
    text-align: center;
    font-size: 25px;
    font-weight: 800;
    color: white;
    padding-bottom: 20px;
}

.uploaded-image img,
.uploaded-video video {
    max-width: 100%;
    max-height: 100%;
    width: 60%;
    height: 50vh;
    margin: auto;
    display: block;
    margin-bottom: 30px;
}

@media screen and (max-width: 1050px) {
    .form-content-sm {
        height: 60vh;
        width: 70vw;
        margin: 20px;
        padding: 10px;
    }

    .file-label {
        font-size: 22px;
        margin: 20px;
    }

    .upload-button {
        padding: 10px;
        margin: 5px 10px;
        font-size: 16px;
        font-weight: 700;
    }

    .file-name {
        font-size: 12px;
    }

    h1 {
        font-size: 35px;
    }

    .upload-container {
        width: 100%;
        max-width: 100%;
        margin: 30px 0 30px 0;
        padding: 10px 0;
    }

    .close-button {
        top: 5%;
        right: 10%;
    }

    .uploaded-image img,
    .uploaded-video video {
        width: 80%;
        height: 40vh;
        margin-bottom: 30px;
    }

    h4 {
        font-size: 22px;
    }
}

@media screen and (max-width: 460px) {
    .form-content-sm {
        height: 60vh;
        width: 80vw;
    }

    .file-label {
        font-size: 15px;
    }

    .upload-button {
        padding: 12px 15px;
        margin: 5px 8px;
        font-size: 12px;
        font-weight: 700;
    }

    .file-name {
        font-size: 10px;
    }

    h1 {
        font-size: 25px;
    }

    .close-button {
        top: 10%;
        right: 10%;
        font-size: 10px;
    }

    .uploaded-image img,
    .uploaded-video video {
        width: 80%;
        height: 40vh;
    }

    h4 {
        font-size: 15px;
    }
}